<template>
  <div class="archive-view">
    <div class="header-wrap">
      <div class="container">
        <div class="title-box">
          <span class="title">{{ title }}</span>
        </div>
        <div class="image-box" :style="`background-image: url(${img});`"></div>
        <header class="header-container"></header>
      </div>
    </div>
    <div class="labels-container">
      <div
        v-for="label in labels"
        :key="label.name"
        class="label-box"
        @click="getBlogsByLabel(label)"
      >
        <el-image class="img" :src="label.img" fit="cover" loading="lazy">
          <template #placeholder>
            <img class="img-placeholder" :src="loadingImg" />
          </template>
        </el-image>
        <div class="text-box">
          <span class="text">{{ label.name }}</span>
        </div>
      </div>
      <div v-for="i in 3" :key="i" class="label-box-extrusion"></div>
    </div>
    <div class="blogs-container" v-if="blogs">
      <div class="blog-box" v-for="[number, blog] in [...blogs]" :key="number" @click="$router.push('/archive/' + number)">
        <div class="blog-img">
          <el-image
            class="img"
            :src="blog.img || loadingImg"
            fit="cover"
            loading="lazy"
          >
            <template #placeholder>
              <img class="img-placeholder" :src="loadingImg" />
            </template>
          </el-image>
        </div>
        <div class="blog-content">
          <div class="blog-title">{{ blog.title }}</div>
          <div class="blog-time">
            <i class="icon-schedule" />
            {{ blog.time }}
          </div>
          <div class="blog-intro">{{ blog.intro }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { getBlogs } from "@/api/github";

export default {
  data() {
    return {
      title: "文章分类",
      img: new URL(`@/assets/img/珂朵莉.jpg`, import.meta.url).href,
      loadingImg: new URL(`@/assets/img/loading.gif`, import.meta.url).href,
      labels: [],
      blogs: null,
    };
  },
  created() {
    this.$store.dispatch("getLabels").then((res) => {
      if (res) {
        this.labels = res;
      }
    });
  },
  methods: {
    async getBlogsByLabel(label) {
      // const response = await getBlogs({ page: 1, labels: label });
      // if (response) {
      //   console.log(response);
      // }
      const blogs = await this.$store.dispatch("getBlogsByLabel", label.name);
      this.img = label.img;
      this.title = label.name;
      this.blogs = blogs;
    },
  },
};
</script>

<style lang="scss" scoped>
.archive-view {
  .header-wrap {
    width: 100%;
    padding-top: 40%;
    position: relative;
    .container {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      .image-box {
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        background-size: cover;
        background-position: center;
        background-attachment: scroll;
        z-index: 1;
        &::before {
          content: "";
          position: absolute;
          height: 100%;
          width: 100%;
          left: 0;
          top: 0;
          background: #00000040;
          z-index: 2;
        }
      }
      .title-box {
        display: flex;
        z-index: 3;
        position: relative;
        height: 100%;
        width: 100%;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .title {
          font-size: 48px;
          color: #fff;
          left: 50%;
          font-weight: 600;
          text-shadow: 1px 1px 3px #cbcbcb;
          letter-spacing: 10px;
        }
      }
    }
  }
  .labels-container {
    // background: #fff;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    padding: 12px;
    justify-content: space-between;
    .label-box {
      cursor: pointer;
      width: calc((100% - 24px) / 3);
      height: 160px;
      margin-top: 12px;
      box-shadow: 1px 1px 5px 2px rgb(0 0 0 / 30%);
      border-radius: 4px;
      overflow: hidden;
      .img {
        height: calc(100% - 24px);
        width: 100%;
        display: block;
        .img-placeholder {
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
      }
      .text-box {
        height: 24px;
        width: 100%;
        background: #f0f0f0;
        text-align: center;
        .text {
          font-size: 14px;
          line-height: 24px;
          color: #909399;
        }
      }
    }
    .label-box-extrusion {
      width: calc((100% - 24px) / 3);
      height: 0px;
      margin: 0;
      position: relative;
    }
    @media (max-width: 800px) {
      .label-box {
        width: calc((100% - 12px) / 2);
        height: 160px;
      }
      .label-box-extrusion {
        width: calc((100% - 12px) / 2);
      }
    }
  }
  .blogs-container {
    // background: #fff;
    position: relative;
    padding: 0 12px;
    .blog-box {
      cursor: pointer;
      height: 173px;
      padding: 16px 0;
      margin-bottom: 8px;
      // border-bottom: 1px solid #ebeef5;
      display: flex;
      flex-direction: row;
      position: relative;
      background: rgb(255 255 255/ 70%);
      border-radius: 10px;
      .blog-img {
        margin-left: 8px;
        height: 140px;
        width: 210px;
        border-radius: 10px;
        overflow: hidden;
        flex-shrink: 0;
        .img {
          height: 100%;
          width: 100%;
        }
      }
      .blog-content {
        height: 140px;
        overflow: hidden;
        margin: 0 12px;
        .blog-title {
          height: 24px;
          margin-bottom: 4px;
          line-height: 20px;
          font-size: 16px;
          color: #333;
          overflow: hidden;
          text-overflow: ellipsis;
          width: 100%;
          white-space: nowrap;
          font-weight: 600;
        }
        .blog-time {
          font-size: 14px;
          color: #888;
          line-height: 20px;
          margin-bottom: 10px;
        }
        .blog-intro {
          font-size: 14px;

          color: #424242;
          height: 60px;
          line-height: 20px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          overflow-wrap: break-word;
        }
      }
    }

    .blog-box:hover {
      &::before {
        position: absolute;
        content: "";
        height: 100%;
        width: 4px;
        background: #fe9600;
        left: -12px;
        top: 0px;
      }
      .blog-content {
        .blog-title {
          color: #fe9600;
        }
      }
    }
  }
}
</style>